import React, {Component} from 'react';
import {Button, Form, Input, Icon, Checkbox, Tooltip, Row, Col} from 'antd';
import {jrFetchPost} from "../common";
import {cookieConfig, setCookie, checkCookie, getCookie} from '../Cookie';
import md5 from 'md5';
import './index.less';

const FormItem = Form.Item;

class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    componentDidMount() {
        if (checkCookie(cookieConfig)) {
            let userInfo = getCookie(cookieConfig);
            userInfo.user_admin === 'true' ? this.props.history.push(`/admin/adminitemmanage`) : this.props.history.push(`/admin/myitemmanage`);
        }
    }

    render() {
        const {getFieldDecorator} = this.props.form;
        return (
            <div className="login">
                <div className="content">
                    <p className="title"></p>
                    <div className="box">
                        <Form>
                            <FormItem>
                                {
                                    getFieldDecorator('phone', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            },
                                            {
                                                len: 11,
                                                message: ''
                                            },
                                            {
                                                pattern: /^\d+$/g,
                                                message: ''
                                            },
                                        ]
                                    })(
                                        <Input prefix={<Icon type="user"/>} placeholder={''}/>
                                    )
                                }

                            </FormItem>
                            <FormItem>
                                {
                                    getFieldDecorator('password', {
                                        initialValue: '',
                                        rules: [
                                            {
                                                required: true,
                                                message: ''
                                            }
                                        ]
                                    })(
                                        <Input type="password" prefix={<Icon type="lock"/>} placeholder={''}/>
                                    )
                                }
                            </FormItem>
                            <Row>
                                <Col xs={{span: 24}} sm={{span: 12}}>
                                    <FormItem>
                                        {
                                            getFieldDecorator('remember', {
                                                valuePropName: 'checked',
                                                initialValue: true
                                            })(
                                                <Checkbox className={'setpswd'}></Checkbox>
                                            )
                                        }
                                    </FormItem>
                                </Col>
                                <Col xs={{span: 24}} sm={{span: 12}}>
                                    <Tooltip placement="bottomRight" title={
                                        'admin,admin'
                                    }>
                                        <p className={'forget'}></p>
                                    </Tooltip>
                                </Col>
                            </Row>
                            <FormItem>
                                <Button onClick={this.handleSubmit} className={'button'}></Button>
                            </FormItem>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }

    handleSubmit = () => {
        let userInfo = this.props.form.getFieldsValue();
        let {password, phone} = userInfo;
        let loading = false;
        password = md5(password)
        this.props.form.validateFields((err, values) => {
            if (!err) {
                jrFetchPost(' /ng-lingxi/api/user/login', {
                    password, phone
                }, loading).then(ret => {
                    let userInfo = {...ret.data, phone, password};
                    setCookie(userInfo);
                    userInfo.user_admin === 'true' ? this.props.history.push(`/admin/adminitemmanage`) : this.props.history.push(`/admin/myitemmanage`);
                }).catch((err) => {
                    console.log(err)
                })
            }

        })
    }
}

export default Form.create()(Index);