import md5 from 'md5';
import {jrFetchPost, jrFetchGet} from '../common';
import React, {PureComponent} from 'react'
import {getCookie,cookieConfig} from '../Cookie';

import {Form, Input, Button, message, Row, Col} from 'antd'


const FormItem = Form.Item;

class AccountManage extends PureComponent {
    constructor(props, context) {
        super(props, context);
        this.state={}
    }


    componentDidMount() {
        const userInfo = getCookie(cookieConfig);
           this.setState({
               phone:userInfo.phone
           })
    }

    render() {
        const {form} = this.props;
        const {getFieldDecorator} = form;
        const formItemLayoutShort = {
            labelCol: {span: 6},
            wrapperCol: {span: 8},
        };
        const formItemLayoutButton = {
            wrapperCol: {offset: 6},
        };
        return (
            <div className='mainview'>
                <Row>
                    <Col span={22} offset={1}>
                        <h1 className={'title'}></h1>
                    </Col>
                </Row>
                <div className='container'>
                    <Form style={{marginTop: 50}}>
                        <FormItem
                            {...formItemLayoutShort}
                            label=""
                        >
                            <Input disabled={true} value={this.state.phone}/>
                        </FormItem>
                        <FormItem
                            {...formItemLayoutShort}
                            label=""
                        >
                            {
                                getFieldDecorator("old_pwd", {
                                    rules: [
                                        {required: true, message: ''},
                                    ],
                                })(
                                    <Input
                                        placeholder=""
                                    />
                                )
                            }
                        </FormItem>
                        <FormItem
                            {...formItemLayoutShort}
                            label=""
                        >
                            {
                                getFieldDecorator("new_pwd_set", {
                                    rules: [
                                        {required: true, message: ''},
                                    ],
                                })(
                                    <Input
                                        placeholder="6-16"
                                    />
                                )
                            }
                        </FormItem>
                        <FormItem
                            {...formItemLayoutShort}
                            label=""
                        >
                            {
                                getFieldDecorator("new_pwd", {
                                    rules: [
                                        {required: true, message: ''},
                                    ],
                                })(
                                    <Input
                                        placeholder=""
                                    />
                                )
                            }
                        </FormItem>
                        <FormItem
                            {...formItemLayoutButton}
                        >
                            <Button style={{marginRight: 20}} onClick={this.handleClear}></Button>
                            <Button type="primary" onClick={this.handleSubmit}></Button>
                        </FormItem>
                    </Form>
                </div>


            </div>
        )
    }

    //
    handleSubmit = () => {
        const {form} = this.props;
        form.validateFields((err) => {
            if (!err) {
                const formObj = form.getFieldsValue();
                if (formObj.new_pwd_set != formObj.new_pwd) {
                    message.warn("");
                    return;
                }
                /*jrFetchPost("/ng-open_platform/api/changePwd", {
                    new_pwd: md5(formObj.new_pwd + ".mifeng888"),
                    old_pwd: md5(formObj.old_pwd + ".mifeng888"),
                }).then((ret) => {

                    message.info("");
                })*/
            }
        });
    }
    //
    handleClear = () => {
        const {form} = this.props;
        form.resetFields();
    }
}


export default Form.create()(AccountManage);