import React, {Component} from 'react';
import {Form, Popconfirm, Table, message} from 'antd'
import {jrFetchGet} from '../common';
import {withRouter} from 'react-router'

class TableListConfig extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataSource: [],
            more: false
        }
    }

    componentDidMount() {
        this.getUserList()
    }

    componentWillReceiveProps(nextProps, nextContext) {
        let dataSource = nextProps.dataSource;
        if (dataSource.constructor === Object) {
            dataSource = Object.values(nextProps.dataSource)
        }
        dataSource.length > 0 && this.setState({
            dataSource
        })
    }

    render() {
        const columns = [
            {
                title: 'ID',
                align: 'center',
                width: 20,
                dataIndex: 'id',
                defaultSortOrder: 'ascend',
                sorter: (a, b) => a.id - b.id,
            },
            {
                title: '',
                align: 'center',
                width: 100,
                dataIndex: 'name'
            },
            {
                title: '',
                width: 100,
                align: 'center',
                dataIndex: 'phone'
            },
            {
                title: '',
                width: 100,
                align: 'center',
                dataIndex: 'email'
            },
            {
                title: '',
                align: 'center',
                width:350,
                className:'tableword',
                render: (t, r, i) => {
                    const {projects} = t;
                    return (
                        <div style={{width: '100%', wordWrap: 'break-word'}}>
                            {
                                !!projects.length && projects.map((it, index) => {
                                        if (projects.length > 3) {
                                            if (index < 3) {
                                                if (index == 2) {
                                                    return <span key={it.id}>
                                                            {it.name}&nbsp;
                                                        {
                                                            !this.state.more && <a onClick={this.getProjects}>
                                                                
                                                            </a>
                                                        }
                                                    </span>
                                                }
                                                return <span key={it.id}>{it.name}&nbsp;</span>
                                            } else {
                                                if(index == projects.length-1){
                                                    return this.state.more && <span key={it.id}>
                                                            {it.name}&nbsp;
                                                            <a onClick={this.getProjects}>
                                                                
                                                            </a>
                                                    </span>
                                                }
                                                return this.state.more && <span key={it.id}>{it.name}&nbsp;</span>
                                            }
                                        } else {
                                            return <span key={it.id}>{it.name}&nbsp;</span>
                                        }
                                    }
                                )
                            }

                        </div>
                    )
                }
            },
            {
                title: '',
                align: 'center',
                width: 200,
                dataIndex: 'is_active',
                render: (is_active, record) => {
                    let config = {
                        '0': '',
                        '1': '',
                        '2': "",
                        '3': "",
                        '4': "",
                    };
                    return (
                        <span>
                            {
                                !is_active && <Popconfirm title="" onConfirm={
                                    () => this.handleDelete(record.id, config['1'])
                                } okText="" cancelText="">
                                    <a href="#"></a>
                                </Popconfirm>
                            }
                            &nbsp;&nbsp;
                            <Popconfirm title="" onConfirm={
                                () => this.handleEdit(record.id, config['2'])
                            }
                                        okText=""
                                        cancelText="">
                                <a></a>
                            </Popconfirm>
                            &nbsp;&nbsp;
                            {
                                !!is_active ? <Popconfirm title="" onConfirm={
                                    () => this.userOff(record.id, config['0'])
                                }
                                                          okText=""
                                                          cancelText="">
                                    <a href="#"></a>
                                </Popconfirm> : <Popconfirm title="" onConfirm={
                                    () => this.userOn(record.id, config['3'])
                                }
                                                            okText=""
                                                            cancelText="">
                                    <a href="#"></a>
                                </Popconfirm>
                            }&nbsp;&nbsp;
                            <Popconfirm title="" onConfirm={
                                () => this.userReset(record.id, config['4'])
                            }
                                        okText=""
                                        cancelText="">
                                <a href="#"></a>
                            </Popconfirm>
                        </span>

                    )
                }
            },
        ]
        return (
            <Table
                columns={columns}
                pagination={false}
                rowKey={'id'}
                bordered
                dataSource={this.state.dataSource || []}
            />
        );
    }

    //
    getUserList = () => {
        jrFetchGet('/ng-lingxi/api/user/list', {}).then((ret) => {
            this.setState({
                dataSource: ret.data
            })
        })
    }
    //
    overallMessage = (ret, text) => {
        ret.code === 0 ? message.success(text + '') : message.success(text + '')
    }
    //
    handleDelete = (id, text) => {
        let url = `/ng-lingxi/api/user/delete/${id}`;
        jrFetchGet(url).then((ret) => {
            this.overallMessage(ret, text);
            this.getUserList()
        })
    }
    //
    handleEdit = (id) => {
        this.props.history.push(`/admin/adduser/?id=${id}`);
    }
    //
    userOff = (id, text) => {
        let url = `/ng-lingxi/api/user/freeze/${id}`;
        jrFetchGet(url).then((ret) => {
            this.overallMessage(ret, text);
            this.getUserList()
        })
    }
    //
    userOn = (id, text) => {
        let url = `/ng-lingxi/api/user/activate/${id}`;
        jrFetchGet(url).then((ret) => {
            this.overallMessage(ret, text);
            this.getUserList()
        })
    }
    //
    userReset = (id, text) => {
        let url = `/ng-lingxi/api/user/reset/password/${id}`;
        jrFetchGet(url).then((ret) => {
            this.overallMessage(ret, text);
        })
    }
    getProjects = () => {
        this.setState({
            more: !this.state.more
        })
    }

}

const Tablelist = withRouter(TableListConfig)
export default Form.create()(Tablelist);

